.footer {
  background-color: $background-footer;
  padding-top: 30px;
  padding-bottom: 30px;

  &__content {
    display: flex;
    justify-content: center;
  }

  &__icon {
    fill: $goit-color;
    width: 13px;
    height: 13px;
    margin-left: 6px;
    margin-right: 6px;
  }

  &__text {
    color: $secondary-color;
    font-size: 16px;
  }

  &__favorite {
    display: flex;
  }
  &__description:hover {
    scale: (1.1);
    cursor: pointer;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    &__content {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }

    &__text {
      font-size: 14px;
    }
  }
}
